@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&display=swap');
.loading{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    width: 100%;
    z-index: 99;
    background: #070A52;
    display: grid;
    place-items: center;
    font-family: 'Josefin Sans', sans-serif;
    &__box{
        position: relative;
        width: 500px;
        height: 200px;
        border: 3px solid #6cff8d;
        
    }
}

